import React, { Component } from 'react';

class Articles extends Component {

  render() {
    return (
      <div id='articleDiv'>
      <blockquote class="embedly-card"><h4><a href="https://medium.com/@nickwhite1423/introduction-to-web-programming-part-5-css-selectors-23058856499f">Introduction to Web Programming-Part 5 (CSS Selectors)</a></h4><p>The previous article can be found at the link below. https://medium.com/@nickwhite1423/introduction-to-web-programming-part-4-google-fonts-92681396c780 In my last post we went over how to add Google Fonts to our personal websites. Here's a look at where we left off. The image above perfectly encapsulates the power that Google Fonts can bring your projects.</p></blockquote>
        <blockquote class="embedly-card"><h4><a href="https://medium.com/@nickwhite1423/introduction-to-web-programming-part-4-google-fonts-92681396c780">Introduction to Web Programming-Part 4 (Google Fonts)</a></h4><p>The previous article can be found at the link below. https://medium.com/@nickwhite1423/introduction-to-web-programming-part-3-css-basics-e002e582f0cb In my last post we went over how to add CSS to our personal websites. Here's a look at where we left off. Our site is almost starting to look like a flag. Not what we want...</p></blockquote>
        <blockquote class="embedly-card"><h4><a href="https://medium.com/@nickwhite1423/introduction-to-web-programming-part-3-css-basics-e002e582f0cb">Introduction to Web Programming-Part 3 (CSS Basics)</a></h4><p>In my last post we went over some of the most common HTML elements as we created our personal websites. This might be a cool site back in 2001, but it's 2018 and it's very important that our site has...</p></blockquote>
        <blockquote class="embedly-card"><h4><a href="https://medium.com/@nickwhite1423/introduction-to-web-programming-part-2-html-basics-5125cab065ea">Introduction to Web Programming-Part 2 (HTML basics)</a></h4><p>In my last article we began our journey into the world of web computing by building a simple Hello World application. This time we're going to be getting a little bit more involved. As we already know, HTML (HyperText Markup Language) is a standard programming language used build things such as websites or web applications.</p></blockquote>
        <blockquote class="embedly-card"><h4><a href="https://medium.com/@nickwhite1423/introduction-to-web-programming-6b0fc149c3db">Introduction to Web Programming - Nick White - Medium</a></h4><p>This is going to be a series of articles dedicated to teaching the basics of web programming. Articles in this series are going to be posted twice every week. Computer Science is a newer discipline springing to existence within the last 100 years.</p></blockquote>
      </div>
    );
  }
}

export default Articles;



